import Vue from 'vue'
import Router from 'vue-router'



Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: () => import("../page/index.vue"),
      children: [
        {
          path: '/',
          name: '首页',
          component: () => import("../page/index/index.vue")
        },
        {
          path: '/dispatch',
          // name: '调度中心',
          component: () => import("../page/dispatch/index.vue"),
          children: [
            {
              path: '/',
              name: '对内应急保障中心',
              component: () => import("../page/dispatch/internal.vue")
            },
            {
              path: 'electricity',
              name: '备电客户保障中心',
              component: () => import("../page/dispatch/electricity.vue")
            },
            {
              path: 'powerOutages',
              name: '电力停电预警中心',
              component: () => import("../page/dispatch/powerOutages.vue")
            },
            {
              path: 'energy',
              name: '基站能源均衡中心',
              component: () => import("../page/dispatch/energy.vue")
            },
          ]
        },
        {
          path: '/monitoring',
          component: () => import("../page/monitoring/index.vue"),
          children: [
            {
              path: '/',
              name: '监控中心 - 能源生产保障',
              component: () => import("../page/monitoring/homePage.vue")
            },
            {
              path: 'electricity',
              name: '能源备电监控',
              component: () => import("../page/monitoring/electricity.vue")
            },
            {
              path: 'powerGeneration',
              name: '能源发电监控',
              component: () => import("../page/monitoring/powerGeneration.vue")
            },
          ]
        },
        {
          path: '/business',
          component: () => import("../page/business/index.vue"),
          children: [
            {
              path: '/',
              name: '业务中心 - 能源生产保障',
              component: () => import("../page/business/homePage.vue")
            },
            {
              path: 'powerGeneration',
              name: '能源发电业务',
              component: () => import("../page/business/powerGeneration.vue")
            },
            {
              path: 'electricity',
              name: '能源备电业务',
              component: () => import("../page/business/electricity.vue")
            },
          ]
        },
        {
          path: '/resources',
          component: () => import("../page/resources/index.vue"),
          children: [
            {
              path: '/',
              name: '资源中心',
              component: () => import("../page/resources/homePage.vue")
            },
          ]
        },
        {
          path: '/operating',
          component: () => import("../page/operating/index.vue"),
          children: [
            {
              path: '/',
              name: '运营中心',
              component: () => import("../page/operating/homePage.vue")
            },
          ]
        },
        {
          path: '/customer',
          component: () => import("../page/customer/index.vue"),
          children: [
            {
              path: '/',
              name: '客户中心 - 客户维护',
              component: () => import("../page/customer/maintenance/index.vue"),
              children:[
                {
                  path: '/',
                  component: () => import("../page/customer/maintenance/maintenance.vue"),
                },
                {
                  path: 'add',
                  name: '新建客户',
                  component: () => import("../page/customer/maintenance/add.vue"),
                }
              ]
            },
            {
              path: 'statistical',
              name: '客户中心 - 客户统计',
              component: () => import("../page/customer/statistical.vue")
            },
            {
              path: 'management',
              name: '客户中心 - 客户管理',
              component: () => import("../page/customer/management.vue")
            },
            {
              path: 'personal',
              name: '个人中心',
              component: () => import("../page/customer/personal.vue")
            },
          ]
        },
      ]
    },
    {
      path: '/chartDetails',
      name: '数据详情',
      component: () => import("../page/chartDetails/beidian.vue")
    },
   
  ]
})



