import axios from "axios";
import qs from 'qs'
// import {
// 	PUBLIC
// } from "@/consts";
import * as request from "./RequestClient";
const MAP_KEY = "CokBuD8tQakmb3ab9heR6gi9DDZ6u0zi";

export default {
	install(Vue, pluginOptions = {}) {
		let requestClient = new request.Client({
			baseURL: 'http://www.baoxingtech.com:9604/'
		}, {
				tokenGetter: () => {
					var cookies = document.cookie;
					var list = cookies.split("; ");          // 解析出名/值对列表

					for (var i = 0; i < list.length; i++) {
						var arr = list[i].split("=");          // 解析出名和值
						if (arr[0] == 'adminToken')
							return decodeURIComponent(arr[1]);   // 对cookie值解码
					}
					return "";
					// let store = localStorage.getItem("store");
					// let token = null;
					// if (store != null) {
					// 	store = JSON.parse(store);
					// 	if (store.userInfo && store.userInfo.token) {
					// 		token = store.userInfo.token;
					// 	}
					// }
					// return token;

					
				},
				globalErrorHandler: error => {
					Vue.prototype.loadingClose()
					if (error.message) {
						Vue.prototype.toasts(error.message)
					}
					return Promise.reject(error);
				},
				errCode: res => {
					Vue.prototype.loadingClose()
					if (res.message) {
						Vue.prototype.toasts(res.message)
					}
					return Promise.reject(res);
				},
			});
		Vue.prototype.Api = {
			//登录
			login(info) {
				return requestClient.fetch(
					request.METHOD_GET,
					`/sys/panel/query/hdg`, {
						userName: info.userName,
						userPwd: info.userPwd
					},
				)
			},
			/**
			 * 备电详情
			 */
			panel_detail_bd(info) {
				return requestClient.fetch(
					request.METHOD_GET,
					`/sys/panel_detail/bd`, {
						panelId: info.panelId,
					},
				)
			},
			/**
			 * 电池
			 */
			queryBattery(info) {
				return requestClient.fetch(
					request.METHOD_GET,
					`/sys/panel_detail/queryBattery`, {
						panelId: info.panelId,
					},
				)
			},
			/**
			 * 图片
			 */
			queryAppFile(info) {
				return requestClient.fetch(
					request.METHOD_GET,
					`/sys/panel_detail/queryAppFile`, {
						panelId: info.panelId,
					},
				)
			},
			/**
			 * 负责人
			 */
			queryContacts(info) {
				return requestClient.fetch(
					request.METHOD_GET,
					`/sys/panel_detail/queryContacts`, {
						panelId: info.panelId,
					},
				)
			},


		};
	}
};





